<template>
    <v-container>
        <v-toolbar flat color="white">
            <v-toolbar-title>Crud Atma pedia</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="search" single-line hide-details></v-text-field>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on }">
                    <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                    </v-card-title>
                    <v-card-text>
                        <v-container>
                            <v-row>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.name" label="Dessert name">
                                    </v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-select :items="items" v-model="editedItem.categories" label="categories">
                                    </v-select>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.fat" label="Fat (g)">
                                    </v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)">
                                    </v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.protein" label="Protein (g)">
                                    </v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                        <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-toolbar>
        <v-row>
            <v-col cols="3" v-for="item in minuman" :key="item.name">
                <v-card max-width="344" outlined class="d-inline-flex">
                    <v-list-item three-line>
                        <v-list-item-content>
                            <div class="overline mb-4">Minuman</div>
                            <v-list-item-title class="headline mb-1">{{item.name}}</v-list-item-title>
                            <v-list-item-subtitle>Categories: {{item.categories}}</v-list-item-subtitle>
                            <v-list-item-subtitle>Fat: {{item.fat}}</v-list-item-subtitle>
                            <v-list-item-subtitle>Carbs: {{item.carbs}}</v-list-item-subtitle>
                            <v-list-item-subtitle>Protein: {{item.protein}}</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <v-icon class="mr-1" color="teal" @click="editItem(item)">mdi-silverware</v-icon>
                        <v-icon class="mr-1" color="red" @click="deleteItem(item)">mdi-delete</v-icon>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>
                    <script>
                      import { minumanRef } from '../firebase';
                      export default{
                        data: () => 
                          ({search: '',
                            selected: [],
                            cek : -1,
                            dialog: false,
                            headers: [
                              {text: 'Dessert (100g serving)',align: 'left',sortable: false,value: 'name',},
                              { text: 'Categories', value: 'categories' },
                              { text: 'Fat (g)', value: 'fat' },
                              { text: 'Carbs (g)', value: 'carbs' },
                              { text: 'Protein (g)', value: 'protein' },
                              { text: 'Actions', value: 'action', sortable: false },
                            ],
                            minuman: [],
                            editedIndex: -1,
                            deleteIndex: -1,
                            editedItem: {name: '',categories: '',fat: 0,carbs: 0,protein: 0,},
                            defaultItem: {name: '',categories: '',fat: 0,carbs: 0,protein: 0,},
                            items: ['es','panas']
                          }),
                        firebase: {
                          minuman : minumanRef
                        },
                        computed: {formTitle () 
                          {return this.cek === -1 ? 'New Item' : 'Edit Item'},
                        },
                        watch: {dialog (val) 
                          {val || this.close()},
                        },
                        methods: {
                          close () {
                            this.dialog = false
                            setTimeout(() => {
                              this.editedItem = Object.assign({}, this.defaultItem)
                              this.editedIndex = -1
                              this.deleteIndex = -1
                            }, 300)
                          },
                          clear(){
                            this.editedItem={}
                            this.editedItem.name= ''
                            this.editedItem.categories= ''
                            this.editedItem.fat= 0
                            this.editedItem.carbs= 0
                            this.editedItem.protein= 0
                            this.cek= -1
                          },
                          // methods untuk save
                          save () {
                            if (this.cek > -1){
                              minumanRef.child(this.editedIndex).set({name: this.editedItem.name, categories: this.editedItem.categories, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
                              this.clear()
                            } else {
                              minumanRef.push({name: this.editedItem.name, categories: this.editedItem.categories, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
                              this.clear()
                            }
                            this.close()
                          },
                          checkboxes (item) {
                            if (this.deleteIndex > -1) {
                              Object.assign(this.selected['.key'], item)
                            } else {
                              this.selected.push(item['.key'])
                            }
                            this.close()
                          },
                          editItem (item) {
                            this.editedIndex = item['.key']
                            this.cek = 0
                            this.editedItem = Object.assign({}, item)
                            this.dialog = true
                          },
                          deleteItem (item) {
                            confirm('Are you sure you want to delete this item?') && minumanRef.child((item['.key'])).remove()
                          }
                        },
                      }
                    </script>